/** @desc   */
export const createLightPillar = (options: {
  x: number;
  y: number;
  z: number;
  color: string;
}) => {
  const height = 10;
  const width = 5;
  const geometry = new THREE.PlaneGeometry(width, height);

  geometry.rotateX(Math.PI / 2);
  geometry.translate(0, 0, height / 2);
  const material = new THREE.MeshBasicMaterial({
    map: new THREE.TextureLoader().load(light_column),
    color: new THREE.Color(options.color || "red"),
    transparent: true,
    side: THREE.DoubleSide,
    depthWrite: false,
  });
  const mesh = new THREE.Mesh(geometry, material);
  const group = new THREE.Group();
  group.name = "";
  // 
  group.add(mesh); //xmeshz
  group.add(mesh.clone().rotateZ(Math.PI / 2));
  group.position.set(options.x, options.y, options.z); //mesh
  const coordVec3 = new THREE.Vector3(
    options.x,
    options.y,
    options.z
  ).normalize();
  const meshNormal = new THREE.Vector3(0, 0, 1);
  group.quaternion.setFromUnitVectors(meshNormal, coordVec3);

  return group;
};
/** @desc   */
export const createWaveMesh = (options: {
  radius: number;
  x: number;
  y: number;
  z: number;
  color?: string;
}) => {
  const geometry = new THREE.PlaneGeometry(1, 1); //XOY
  const texture = new THREE.TextureLoader().load(aperture);
  const material = new THREE.MeshBasicMaterial({
    color: options.color || "red",
    map: texture,
    transparent: true, //png
    opacity: 1.0,
    depthWrite: false, //
  });
  const mesh = new THREE.Mesh(geometry, material);

  // 
  const size = options.radius * 0.2; //Mesh
  mesh.scale.set(size, size, size); //mesh
  mesh.userData["size"] = size; //mesh
  mesh.userData["scale"] = Math.random() * 1.0; //._smesh  mesh.size1~2
  mesh.position.set(options.x, options.y, options.z);
  const coordVec3 = new THREE.Vector3(
    options.x,
    options.y,
    options.z
  ).normalize();
  const meshNormal = new THREE.Vector3(0, 0, 1);
  mesh.quaternion.setFromUnitVectors(meshNormal, coordVec3);
  return mesh;
};
