import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
export default function createSceneCameraRenderer(el: HTMLElement) {
  const dom = el;
  const width = dom.clientWidth;
  const height = dom.clientHeight;
  const scene = new THREE.Scene(); //

  const camera = new THREE.PerspectiveCamera(45, width / height, 1, 1000); //()
  camera.position.set(0, 90, 300); //
  camera.lookAt(0, 0, 0);
  //
  const renderer = new THREE.WebGLRenderer({
    antialias: true, //
    alpha: true, //
  });
  renderer.setClearColor(0x000000, 0); //
  renderer.setSize(width, height); //

  const orbitControls = new OrbitControls(camera, renderer.domElement);
  orbitControls.autoRotate = true; //()
  orbitControls.enableRotate = true; //
  orbitControls.enableZoom = true; //
  orbitControls.autoRotateSpeed = 15 / 30; //
  orbitControls.enablePan = false; //
  orbitControls.enableDamping = true; //
  orbitControls.dampingFactor = 0.05; //(.25)

  orbitControls.minDistance = 250; //
  orbitControls.maxDistance = 400; //
  dom.appendChild(renderer.domElement); //domcanvas
  return {
    scene,
    orbitControls,
    renderer,
    camera,
    render: () => {
      renderer.render(scene, camera);
      orbitControls.update(); //
    },
  };
}
